#color, #color2 {
    color:#54AC9A;
    font-size: clamp(12px, 4vw, 26px);
    line-height: 0.9;

}


#font {
    font-size: clamp(8px, 2vw, 20px);
    line-height: 1.1;
}

#grey p{
    color: #898989;
    font-size: clamp(26px, 5vw, 80px);
    line-height: 0.9;
}

@media (min-width: 1280px) {
    
    #grey p {
        font-size: clamp(5vw, 6vw, 8vw);
        
    }

    #color, 
    #color2 {
        font-size: clamp(12px, 3vw, 26px);
    }

    #font {
        font-size: clamp(8px, 3vw, 20px);
    }
    
}