.main-page {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    position: relative;
}

.text-overlay {
    grid-row: 1;
    grid-column: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


#transparent {
    background: linear-gradient(244.39deg, #C4C4C4 26.1%, rgba(255, 255, 255, 0.1) 54.06%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    mix-blend-mode: screen;

}

#transparent2 {
    background: linear-gradient(274.99deg, #C4C4C4 10.21%, #C4C4C4 10.8%, rgba(255, 255, 255, 0.05) 103.99%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-blend-mode: lighten;
    mix-blend-mode: screen;
}

#transparent,
#transparent2 {
    font-size: clamp(22px, 5vw, 80px);
    line-height: 0.9;
}

nav ul {
    font-size: 9px;
}

.menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    width: fit-content;
    align-items: center;
    font-weight: 300;
    --tw-bg-opacity: 1;
    background-color: rgb(0 0 0 / var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgb(255 255 255 / var(--tw-text-opacity));
    top: 3rem;
    margin:auto;
    left: 0px;
    z-index: 20;
    right: 0px;
}

.navbar {
    margin-top: 0.75rem;
    line-height: 1.25rem;
}

.image {
    width: 8rem;
    height: 8rem;
    margin-top: 0.75rem;
    object-fit: contain;

}

.sample-enter {
    opacity: 0;
}

.sample-enter-active {
    opacity: 1;
    transition: opacity 600ms;
}

.sample-exit {
    opacity: 1;
}

.sample-exit-active {
    opacity: 0;
    transition: opacity 600ms;
}


@media (min-width: 1280px) {
    
    #transparent,
    #transparent2 {
        font-size: clamp(5vw, 6vw, 8vw);
    }

    nav ul {
        font-size: 20px;
    }
     
    .image {
        width: 14rem;
        height: 14rem;
        object-fit: contain;
    }

    .navbar {
        margin-top: 0.95rem;
        line-height: 1.45rem;
    }

    .menu {
        top: 6rem;
    }

}



